import './App.css';
import React from "react";
// import ReactDOM from "react-dom";
import "./index.css";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import Navbar from "./components/Navbar/index";
import Footer from "./components/Footer/index";
import Home from "./components/Header/index";
import ProjGal from "./components/Projects/Gallery/Gallery.js";
import PDF from "./components/Projects/viewPDF";
import Doc from "./components/Projects/viewGuide";

function App() {
  return (
    <Router>
    <Navbar />
 
      <Route exact path="/" component={Home}><Home /> </Route>
      <Switch>
      {/* <Route path="/home" component={Home}><Home /></Route> */}
      <Route path="/work" component={ProjGal}><ProjGal /></Route>
      <Route path="/resume" component={PDF}><PDF /></Route>
      <Route path="/guide" component={Doc}><Doc /></Route>
    </Switch>
    <Footer />
</Router>

  );
}

export default App;


